<template>
  <div>
    <div
      v-if="showReportLoader"
      class="spinner-grow text-center"
      style="width: 3rem; height: 3rem;"
      role="status"
    >
      <span class="sr-only">Loading...</span>
    </div>
    <div v-else>
      <button @click="getSelectedRows()" v-bind="{ shape: 'pill' }">
        View Selected
      </button>

      <ag-grid-vue
        style="width: 100%"
        domLayout="autoHeight"
        class="ag-theme-alpine"
        :columnDefs="columnDefs"
        :rowData="rowData"
        rowSelection="single"
        @grid-ready="onGridReady"
      >
      </ag-grid-vue>

      <!-- <CRow>
        <CCol
          sm="6"
          lg="3"
          v-for="report in reportData.resources"
          v-bind:key="report.id"
        >
          <CWidgetProgress>
            <div class="h4 m-0">{{ report.name }}</div>
            <div>Group: {{ report.rpt_group }}</div>
            
          </CWidgetProgress>
        </CCol>
      </CRow> -->
    </div>
  </div>
</template>

<script>
import axiosInstance from "../../service/axios-instance.js";
import { mapGetters } from "vuex";
import { AgGridVue } from "ag-grid-vue";

export default {
  name: "Report",
  data() {
    return {
      columnDefs: null,
    };
  },
  components: {
    AgGridVue,
  },
  methods: {
    onGridReady(params) {
      this.gridApi = params.api;
      this.columnApi = params.columnApi;
    },
    getSelectedRows() {
      const selectedNodes = this.gridApi.getSelectedNodes();
      console.log("selectedNodes: ", selectedNodes);
      this.$store
        .dispatch("callGetSelectedReport", selectedNodes[0].data)
        .then(() => {
          this.$router.push("report/scheduled-report");
        })
        .catch((error) => {
          // show error message in template
        });
    },
  },
  computed: {
    ...mapGetters([
      // map this.count to store.state.count
      "showReportLoader",
      "reportData",
      "rowData",
    ]),
  },

  mounted() {
    this.columnDefs = [
      {
        headerName: "Title",
        field: "title",
        checkboxSelection: true,
        sortable: true,
        filter: true,
      },
      { headerName: "Group", field: "rpt_group", sortable: true, filter: true },
      {
        headerName: "Created On",
        field: "created_on",
        sortable: true,
      },
    ];

    this.$store.dispatch("callGetReports");
  },
};
</script>
